"use client";

import { useState, useEffect } from "react";
import Image from "next/image";
import { FaBars, FaTimes } from "react-icons/fa";
import Link from "next/link";

export default function Header() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);
  const [headerData, setHeaderData] = useState({
    logo: { src: "", alt: "Logo del sitio" },
    desktopMenu: []
  });
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // Función para obtener los datos del header
  const fetchHeaderData = async () => {
    setIsLoading(true);
    try {
      const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/json/header`, { method: "GET" });
      
      const data = await response.json();
      
      // Adaptar los datos según la estructura proporcionada
      setHeaderData({
        logo: data["section-header"].logo,
        desktopMenu: data["section-header"]["desktop-menu"]
      });
      
      console.log(data);
      setError(null);
    } catch (err) {
      console.error("Error fetching header data:", err);
      setError("No se pudieron cargar los datos del menú");
    } finally {
      setIsLoading(false);
    }
  };

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };

  // Efecto para obtener los datos del header
  useEffect(() => {
    fetchHeaderData();
  }, []);

  // Efecto para detectar el scroll
  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 10;
      if (isScrolled !== scrolled) {
        setScrolled(isScrolled);
      }
    };
    
    window.addEventListener('scroll', handleScroll);
    handleScroll();
    
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [scrolled]);

  // Verificar si tenemos una URL para el logo
  const hasLogo = headerData.logo && headerData.logo.src;

  return (
    <header className={`navbar-header ${scrolled ? 'scrolled' : ''}`}>
      <nav className="navbar-container">
        {/* Logo */}
        <Link href="/" className="logo-header">
          {hasLogo && !isLoading ? (
            <Image 
              src={"/"}
              alt={headerData.logo.alt || "Logo del sitio"}
              width={200}
              height={64}
              priority
            />
          ) : (
            // Placeholder mientras carga
            <div className="logo-placeholder" style={{ width: 200, height: 64 }}></div>
          )}
        </Link>

        {isLoading ? (
          <div className="loading-indicator"></div>
        ) : (
          <>
            {/* Menú en Desktop */}
            <ul className="desktop-menu">
              {error && <li className="error-message">{error}</li>}
              
              {headerData.desktopMenu.map((item, index) => (
                <li key={index}>
                  <Link href={item.href}>{item.label}</Link>
                </li>
              ))}
              <li>ES ▾</li>
            </ul>

            {/* Botón hamburguesa en Móvil */}
            <button className="menu-toggle" onClick={toggleMenu} aria-label="Toggle menu">
              {menuOpen ? <FaTimes /> : <FaBars />}
            </button>

            {/* Menú desplegable en Móvil */}
            <ul className={`mobile-menu ${menuOpen ? "open" : ""}`}>
              {error && <li className="error-message">{error}</li>}
              
              {headerData.desktopMenu.map((item, index) => (
                <li key={index} onClick={toggleMenu}>
                  <Link href={item.href}>{item.label}</Link>
                </li>
              ))}
              <li>ES ▾</li>
            </ul>
          </>
        )}
      </nav>
    </header>
  );
}