"use client";
import { useEffect, useState, useRef } from "react";

export default function AboutUs() {
  // Estados para los datos
  const [title, setTitle] = useState("");
  const [description, setDescription] = useState("");
  const [ctaText, setCtaText] = useState("");
  const [progressData, setProgressData] = useState([]);
  
  // Estados para control de UI
  const [isVisible, setIsVisible] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [dataLoaded, setDataLoaded] = useState(false);
  const [error, setError] = useState(null);
  const sectionRef = useRef(null);

  // Fetch de los datos
  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await fetch(`${process.env.NEXT_PUBLIC_API_URL}/json/node/1`);
        
        if (!response.ok) {
          throw new Error(`Error en la respuesta: ${response.status}`);
        }
        
        const data = await response.json();
        
        // Verificar si existe la sección about-us
        if (data["section-about-us"]) {
          const aboutData = data["section-about-us"];
          
          // Actualizar estados con los datos del JSON
          setTitle(aboutData.title || "");
          setDescription(aboutData.description || "");
          setCtaText(aboutData.cta?.text || "");
          
          // Verificar y procesar los datos de skills
          if (aboutData.skills && aboutData.skills.list && aboutData.skills.percents) {
            // Crear array de objetos combinando list y percents
            const newProgressData = aboutData.skills.list.map((title, index) => ({
              title: title,
              value: aboutData.skills.percents[index] || 0
            }));
            
            setProgressData(newProgressData);
          }
          
          // Marcar los datos como cargados
          setDataLoaded(true);
        } else {
          throw new Error("No se encontró la sección 'section-about-us'");
        }
        
        setIsLoading(false);
      } catch (err) {
        console.error("Error al obtener los datos:", err);
        setError(err.message);
        setIsLoading(false);
      }
    };
    
    fetchData();
  }, []);

  // Efecto para las animaciones al hacer scroll
  useEffect(() => {
    if (!dataLoaded) return; // No iniciar el observer hasta que los datos estén cargados
    
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setIsVisible(true);
          observer.disconnect();
        }
      },
      { threshold: 0.3 }
    );

    if (sectionRef.current) observer.observe(sectionRef.current);

    return () => observer.disconnect();
  }, [dataLoaded]); // Dependencia en dataLoaded para asegurar que se active después de cargar datos

  // Si está cargando, mostrar indicador
  if (isLoading) {
    return (
      <section id="about" className="loading-section">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Cargando información...</p>
        </div>
      </section>
    );
  }

  // Si hay un error, mostrar mensaje
  if (error) {
    return (
      <section id="about" className="error-section">
        <div className="error-container">
          <p>Error al cargar los datos: {error}</p>
        </div>
      </section>
    );
  }

  // Si no hay datos, mostrar mensaje
  if (!dataLoaded || progressData.length === 0) {
    return (
      <section id="about" className="no-data-section">
        <div className="no-data-container">
          <p>No hay datos disponibles para mostrar</p>
        </div>
      </section>
    );
  }

  return (
    <section id="about" ref={sectionRef}>
      <div className="about-us">
        <div className="about-us-title">
          <h1 className={isVisible ? "fade-in" : ""}>{title}</h1>
          <a className={isVisible ? "fade-in" : ""}>{ctaText}</a>
        </div>

        <div className="about-us-description">
          <div className="about-us-description-progress">
            {progressData.map((item, index) => (
              <div className="tpl-progres" key={index}>
                <div className={`progress-bar ${isVisible ? "slide-in" : ""}`}>
                  <h5>{item.title}</h5>

                  {/* Barra de progreso animada */}
                  <div
                    className="progress-fill"
                    style={{
                      width: isVisible ? `${item.value}%` : "0%",
                      transition: "width 1s ease",
                    }}
                  ></div>

                  <span>{item.value}%</span>
                </div>
              </div>
            ))}
          </div>

          <div className="about-us-description-full">
            <div className="description-full">
              <p className={isVisible ? "fade-in" : ""} 
                 dangerouslySetInnerHTML={{ __html: description }}>
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}